import React from 'react'
import { Link } from 'react-router-dom'

export const HeroCard = ({ id, superhero, publisher, alter_ego, first_appearance, characters }) => {
    return (
        <div className="card col-sm-4 m-3 animate__animated animate__fadeInUp" style={{ maxWidth: '15rem' }}>
            <img src={`https://www.corretajessanpedro.cl/assets/heroes/${id}.jpg`} className="card-img-top" alt="..." />
            <div className="card-body">
                <h5 className="card-title">{superhero}</h5>
                <p className="card-text">{alter_ego}.</p>
                {
                    (alter_ego !== characters) && <p className="card-text">{characters}</p>
                }

                <p className="card-text"><small>{first_appearance}</small></p>

                <Link
                    className="btn btn-primary"
                    to={`./hero/${id}`}
                >Show Details...</Link>

                {/* <a href="#" className="btn btn-primary">Show Details</a> */}
            </div>
        </div>
    )
}