import React, { useMemo } from 'react'
import { Redirect, useParams } from 'react-router'
import { getHeroById } from '../../selectors/getHeroById';

export const HeroScreen = ({ history }) => {

    const { heroeId } = useParams();
    // const hero = getHeroById(heroeId);
    const hero = useMemo(() => getHeroById(heroeId), [heroeId]);

    console.log(hero);


    if (!hero) {
        return <Redirect to="/" />
    }

    const handleReturn = () => {

        history.length <= 2 ? history.push('/') : history.goBack();
    }

    const { superhero, publisher, alter_ego, first_appearance, characters } = hero;

    return (
        <div>

            <div className="card mb-3 mt-3 animate__animated animate__fadeInLeft" style={{ maxWidth: '540px' }}>
                <div className="row g-0">
                    <div className="col-md-6">
                        <img className="img-fluid" src={`https://www.corretajessanpedro.cl/assets/heroes/${heroeId}.jpg`} alt="..." />
                    </div>
                    <div className="col-md-6">
                        <div className="card-body">
                            <h5 className="card-title">{superhero}</h5>
                            <p className="card-text">{publisher}</p>
                            <p className="card-text">{alter_ego}</p>
                            {
                                (alter_ego !== characters) && <p className="card-text">{characters}</p>
                            }
                            <p className="card-text"><small>{first_appearance}</small></p>

                            <button
                                onClick={handleReturn}
                                className="btn btn-outline-info"
                            >Return</button>
                        </div>

                    </div>
                </div>
            </div>
        </div>
    )
}
